import React from "react";
import Affair from "./Affair";
import {AffairType, FilterType} from "./HW2";
import s from "./Affairs.module.css";

type AffairsPropsType = {
    data: Array<AffairType>
    setFilter: (filterValue: FilterType) => void
    deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => {
        props.setFilter("all")
    };
    const setHigh = () => {
        props.setFilter("high")
    };
    const setMiddle = () => {
        props.setFilter("middle")
    };
    const setLow = () => {
        props.setFilter("low")
    };

    return (
        <div>
            <div className={s.myAffairs}>
                {mappedAffairs}
            </div>
            <button onClick={setAll} className={s.priorityButton}>All</button>
            <button onClick={setHigh} className={s.priorityButton}>High</button>
            <button onClick={setMiddle} className={s.priorityButton}>Middle</button>
            <button onClick={setLow} className={s.priorityButton}>Low</button>
        </div>
    );
}

export default Affairs;
